import { BlogCard, Images } from './../components';

const data = Array(3).fill(null).map((d, i) => (
    {
        imgSrc: Images[`blog${i+1}`],
        header: 'What Our Customer',
        content: '',
        goTo: '',
        imgAlt: 'blog-img'
    }
))

export default ({}) => (
    <div className="flex flex-col lg:flex-row justify-center items-cener">
        {
            data.map(i => 
                <BlogCard 
                    imgSrc ={i.imgSrc}
                    header={i.header}
                    content={i.content}
                    goTo={i.goTo}
                    imgAlt={i.imgAlt}
                />
            )
        }
    </div>
)
